<template>
  <div class="col s12 m6 l8">
    <div class="card orange darken-3 bill-card">
      <div class="card-content white-text">
        <div class="card-header">
          <span class="card-title">{{'Home_cur' | localize}}</span>
        </div>
        <table>
          <thead>
            <tr>
              <th>{{'Home_cur_val' | localize}}</th>
              <th>{{'Home_cur_kurs' | localize}}</th>
              <th>{{'Home_cur_date' | localize}}</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="cur in currencies" :key="cur">
              <td>{{cur}}</td>
              <td>{{rates[cur].toFixed(5)}}</td>
              <td>{{date | date('date')}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    props: ['rates', 'date'],
    data: () =>({
        currencies:['KZT', 'RUB', 'USD', 'EUR']
    })
}
</script>